import { Component } from '@angular/core'
import { ControlService } from '../control.service'
import { ConfigService } from '../config.service'

@Component({
  selector: 'modification-layer',
  templateUrl: './modification-layer.component.html',
  styleUrls: ['./modification-layer.component.css'],
  providers: [ControlService],
})
export class ModificationLayerComponent {

  height: number;
  width: number;
  control: ControlService;
  config: ConfigService;

  constructor(control: ControlService, config: ConfigService) {
    this.config = config;
    config.size.subscribe(size => {
      [this.width, this.height] = size;
    });
    this.control = control;
  }

  submit() {
    const image = <ImageData>this.control.reciever.image();
    this.config.submitModification(image);
  }

}



