import { Component, Input, AfterViewInit, ViewChild, ElementRef } from '@angular/core'
import { Http, Response } from '@angular/http'
import { ConfigService } from '../config.service'
import * as _ from 'lodash'
import {Bound} from './bound'

@Component({
  selector: 'base-image',
  templateUrl: './base-image.component.html',
  /* viewProviders: [HTTP_PROVIDERS], */
  styleUrls: ['./base-image.component.css'],
})
export class BaseImageComponent implements AfterViewInit {

  config: ConfigService;
  http: Http;
  ctx: CanvasRenderingContext2D;
  @ViewChild("canvas") canvasRef: ElementRef;
  @Input() height: number;
  @Input() width: number;

  constructor(config: ConfigService, http: Http) {
    this.config = config;
    this.http = http;
    this.config.size.subscribe((size) => {
      const [width, height] = size;
      this.load(width, height);
    })
  }

  ngAfterViewInit() {
    this.ctx = this.canvasRef.nativeElement.getContext('2d');
    this.load(this.width, this.height);
  }

  load(width: number, height: number) {
    const img = <HTMLImageElement>document.createElement('img');
    img.src = this.config.getFrameSrc();
    img.height = height;
    img.width = width;
    img.onload = () => {
      this.ctx.drawImage(img, 0, 0, width, height);
      this.http.get(this.config.getBoundingSrc())
        .subscribe((res) => {
          const objectId = this.config.objectId.replace('obj', '');
          const bound = Bound.parseXML(res.text(), width, height, objectId);
          bound.draw(this.ctx);
        });
    }
  }
}



