import { Component, OnInit } from '@angular/core';
import {ControlService} from '../control.service'


@Component({
  selector: 'app-control-bar',
  templateUrl: './control-bar.component.html',
  styleUrls: ['./control-bar.component.css']
})
export class ControlBarComponent implements OnInit {
  brushSize: number = 5;
  groundCheck: boolean = true;
  control: ControlService;

  constructor(control: ControlService) {
    this.control = control;
    this.control.controller = this;
  }

  reset(){
    this.control.reciever.reset();
  }
  setSize(size: number){
    this.control.reciever.brushSize = size;
  }

  setGround(check: boolean){
    this.control.reciever.brushColor = this.groundColor(check);
  }

  ngOnInit() {
  }

  groundColor(check: boolean):string{
    return check ? "green" : "red";
  }
  groundText(check: boolean):string {
    return check ? "foreground" : "backgroud";
  }
}



