import { Component } from '@angular/core';
import { ConfigService } from '../config.service'

@Component({
  selector: 'verification-layer',
  templateUrl: './verification-layer.component.html',
  styleUrls: ['./verification-layer.component.css'],
})
export class VerificationLayerComponent {
  height: number;
  width: number;
  config: ConfigService;

  constructor(config: ConfigService) {
    this.config = config;
    config.size.subscribe(size=>{
      [this.width, this.height] = size;
    });
  }
  submit(value: boolean) {
    this.config.submitVerification(value);
  }

}



