import { Injectable } from '@angular/core'
import { URLSearchParams, Http } from '@angular/http'
import { S3, config } from "aws-sdk"
import * as aws from 'aws-sdk';
import * as _ from 'lodash'
import { saveAs } from 'file-saver'
import * as crypto from 'crypto-js'
import { Observable } from 'rxjs'
import { imgdata2mask } from './utils/Utils'

window['aws'] = aws;

@Injectable()
export class ConfigService {
  objectId: string;
  videoId: string;
  frameId: string;
  destination: string;
  assignmentId: string;
  page: string;
  height: number;
  width: number;
  bucket: aws.S3;
  size: Observable<[number, number]>;

  constructor(http: Http) {
    let param = new URLSearchParams(window.location.search.slice(1));
    [this.videoId, this.objectId, this.frameId] = decodeURIComponent(param.get('vid')).split(',');
    this.assignmentId = param.get('assignmentId');
    // this.destination = decodeURIComponent(param.get('turkSubmitTo'));
    this.destination = "https://workersandbox.mturk.com/mturk/externalSubmit"
    this.page = param.get('page');
    this.size = Observable.create((observer) => {
      http.get(this.getBoundingSrc()).subscribe(res => {
        const parser = new DOMParser();
        const xml = parser.parseFromString(res.text(), 'text/xml');
        const queryint = (query) => parseInt(xml.querySelector(query).textContent);
        const height = queryint('size height');
        const width = queryint('size width');
        const scale = 600 / width;
        this.width = Math.floor(width * scale);
        this.height = Math.floor(height * scale);
        observer.next([this.width,this.height]);
      });
    });
    this.bucket = new aws.S3({ params: { Bucket: 'bucket-for-annotation-search' } });
    http.get(`assets/credential`).subscribe((res) => {
      const data = JSON.parse(
        crypto.AES.decrypt(res.text(), 'mturk segmentation')
          .toString(crypto.enc.Utf8));
      const credential = new aws.Credentials(data['accessKeyId'], data['secrete']);
      this.bucket.config.update({
        credentials: credential,
        region: 'us-west-2'
      });
      this.bucket.listObjects({
        Bucket: 'bucket-for-annotation-search'
      }).promise().then((x) => console.log(x)).catch((error) => console.log(error));
    });
  }
  getFrameSrc(): string {
    let name = window.location.pathname.replace('index.html', '');
    return `${name}assets/${this.videoId}/${this.objectId}_${this.frameId}.png`;
  }
  getBoundingSrc(): string {
    let name = window.location.pathname.replace('index.html', '');
    return `${name}assets/${this.videoId}/00${this.frameId}.xml`;
  }
  submitVerification(valid: boolean) {
    this.submitForm({
      'formtype': 'verification',
      'assignmentId': this.assignmentId,
      'frameId': this.frameId,
      'objectId': this.objectId,
      'videoId': this.videoId,
      'valid': valid.toString(),
    }, this.destination);
  }
  submitModification(image: ImageData) {
    const [red, green] = imgdata2mask(image);
    const f_red = new File([red], 'red.mask', { type: 'text/binary' });
    const f_green = new File([green], 'green.mask', { type: 'text/binary' });
    const path = `experimentdata/${this.videoId}-${this.objectId}-${this.frameId}-${this.assignmentId}`;
    const n_red = `${path}/green.mask`;
    const n_green = `${path}/red.mask`;
    const metadata = {
        'formtype': 'modification',
        'assignmentId': this.assignmentId,
        'frameId': this.frameId,
        'objectId': this.objectId,
        'videoId': this.videoId,
        'height': image.height,
        'width': image.width,
        'redfilepath': n_red,
        'greenfilepath': n_green,
      };
    const f_meta = new File([JSON.stringify(metadata)], 'meta.json', {type: 'text/json'});
    const n_meta = `${path}/meta.json`;
    Promise.all([
      this.submitS3(f_red, n_red),
      this.submitS3(f_green, n_green),
      this.submitS3(f_meta, n_meta),
    ]).then(_ =>
      this.submitForm(metadata, this.destination));
  }

  private submitS3(file: File, path: string) {

    return this.bucket.putObject({
     Key: path,
     ContentType: file.type,
     Body: file,
     Bucket: 'bucket-for-annotation-search'
    }).promise();

    // return new Promise((resolve, reject) => {
    //   saveAs(file, name);
    //   resolve(null);
    // });

  }
  private submitForm(pair: Object, destination: string) {

    // const text = JSON.stringify(pair);
    // console.log(pair);
    // const file = new File([text], 'form', { type: 'plain/text' });
    // saveAs(file, 'form');

    let form = <HTMLFormElement>document.createElement('form');
    _.each(pair, (value, key) => {
     let input = <HTMLInputElement>document.createElement('input');
     input.type = 'text';
     input.value = value;
     input.name = key;
     form.appendChild(input);
    });
    let button = <HTMLInputElement>document.createElement('input');
    button.type = 'submit';
    form.appendChild(button);
    form.action = this.destination;
    form.method = 'POST';
    document.body.appendChild(form);
    form.submit();

  }
}



