export function imgdata2mask(img: ImageData): [Uint8Array, Uint8Array] {
  const rgba = img.data;
  const size = img.height * img.width;
  const red = new Uint8Array(Math.ceil(size / 8));
  const green = new Uint8Array(Math.ceil(size / 8));

  let store_red = 0;
  let store_green = 0;
  for (let i = 0; i < size; i++) {
    let div = Math.trunc(i / 8);
    let rem = i % 8;
    let r = +(rgba[i * 4 + 0] > 0);
    red[div] += r << rem;
    let g = +(rgba[i * 4 + 1] > 0);
    green[div] += g << rem;
  }
  return [red, green];
}




