import * as _ from 'lodash'
export class Bound {
  x: number;
  y: number;
  width: number;
  height: number;
  name: string;
  constructor(value: { x: number, y: number, width: number, height: number, name: string }) {
    this.x = value.x;
    this.y = value.y;
    this.height = value.height;
    this.width = value.width;
    this.name = value.name;
  }
  scale(xscale: number, yscale: number) {
    this.x = this.x * xscale;
    this.y = this.y * yscale;
    this.width = this.width * xscale;
    this.height = this.height * yscale;
  }
  static getNameFromId(id: string) {
    const id_map = {
      'n02691156': "airplane",
      'n02419796': "antelope",
      'n02131653': "bear",
      'n02834778': "bicycle",
      'n01503061': "bird",
      'n02924116': "bus",
      'n02958343': "car",
      'n02402425': "cattle",
      'n02084071': "dog",
      'n02121808': "domestic_cat",
      'n02503517': "elephant",
      'n02118333': "fox",
      'n02510455': "giant_panda",
      'n02342885': "hamster",
      'n02374451': "horse",
      'n02129165': "lion",
      'n01674464': "lizard",
      'n02484322': "monkey",
      'n03790512': "motorcycle",
      'n02324045': "rabbit",
      'n02509815': "red_panda",
      'n02411705': "sheep",
      'n01726692': "snake",
      'n02355227': "squirrel",
      'n02129604': "tiger",
      'n04468005': "train",
      'n01662784': "turtle",
      'n04530566': "watercraft",
      'n02062744': "whale",
      'n02391049': "zebra",
    };
    return id_map[id];
  }
  static parseXML(res: string, canvas_width: number, canvas_height: number, objectId: string): Bound {
    const parser = new DOMParser();
    const xml = parser.parseFromString(res, 'text/xml');
    let node = xml.querySelector('annotation');
    const queryint = (query) => parseInt(node.querySelector(query).textContent);
    const height = queryint('size height');
    const width = queryint('size width');
    node = _.find(node.querySelectorAll('object'), obj => {
      return obj.querySelector('trackid').textContent == objectId
    });
    const nameid = node.querySelector('name').textContent;
    const name = this.getNameFromId(nameid);
    const xmax = queryint('object xmax');
    const xmin = queryint('object xmin');
    const ymax = queryint('object ymax');
    const ymin = queryint('object ymin');
    const bound = new Bound({
      x: xmin, y: ymin, height: (ymax - ymin), width: (xmax - xmin),
      name: name
    });
    bound.scale(canvas_width / width, canvas_height / height);
    return bound;
  }
  draw(ctx: CanvasRenderingContext2D) {
    ctx.lineWidth = 4;
    ctx.strokeStyle = 'rgba(0,0,0,0.5)';
    ctx.strokeRect(this.x, this.y, this.width, this.height);
    ctx.textAlign = 'center';
    ctx.textBaseline = 'bottom';
    ctx.font = '30px Arial';
    ctx.fillText(this.name, this.x + this.width / 2, this.y);
  }
}




