import { Component, AfterViewInit, Input, ViewChild, ElementRef } from '@angular/core';
import { ControlService } from '../control.service'

@Component({
  selector: 'drawable-canvas',
  templateUrl: './drawable-canvas.component.html',
  styleUrls: ['./drawable-canvas.component.css']
})
export class DrawableCanvasComponent implements AfterViewInit {
  @Input() brushSize: number = 5;
  @Input() height: number;
  @Input() width: number;

  ctx: CanvasRenderingContext2D;
  canvas: HTMLCanvasElement;
  dragging: boolean = false;
  pts: Array<[number, number]> = [];
  brushColor: string = "red";
  control: ControlService;
  @ViewChild("canvas") canvasRef: ElementRef;


  constructor(control: ControlService) {
    this.control = control;
    this.control.reciever = this;
  }

  reset() {
    this.ctx.clearRect(0, 0, this.width, this.height);
  }

  ngAfterViewInit() {
    this.canvas = this.canvasRef.nativeElement;
    this.ctx = this.canvas.getContext('2d');
  }

  dragStart(e: MouseEvent): void {
    this.dragging = true;
    this.ctx.lineWidth = this.brushSize;
    const color = this.brushColor;
    this.ctx.strokeStyle = color;
    this.pts.length = 0;
    this.ctx.globalCompositeOperation = 'source-over';
    this.ctx.lineCap = this.ctx.lineJoin = 'round';
    e.stopPropagation();
  }

  dragMove(e: MouseEvent): void {
    if (this.dragging) {
      const rect = this.canvas.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;
      if (this.pts.length == 2) {
        this.ctx.beginPath();
        this.ctx.bezierCurveTo(this.pts[0][0], this.pts[0][1], this.pts[1][0], this.pts[1][1], x, y);
        this.ctx.stroke();
        this.ctx.moveTo(x, y);
        this.pts.length = 0;
      }
      this.pts.push([x, y]);
    }
  }

  dragEnd(e: MouseEvent): void {
    this.dragging = false;
    this.pts.length = 0;
  }

  image(): ImageData {
    return this.ctx.getImageData(0, 0, this.width, this.height);
  }
}




